img {
  width: 400px;
  border-radius: 10%;
}

.cat-info {
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 30px;
  display: flex;
}

.box {
  max-width: 600px;
}

h1 {
  text-align: center;
  display: block;
}

.hidden {
  display: none;
}

.loader {
  width: 8px;
  height: 40px;
  color: #000;
  box-sizing: border-box;
  background: currentColor;
  border-radius: 4px;
  margin: 20px auto;
  animation: animloader .3s linear .3s infinite alternate;
  display: block;
  position: relative;
}

.loader:after, .loader:before {
  content: "";
  width: 8px;
  height: 40px;
  box-sizing: border-box;
  background: currentColor;
  border-radius: 4px;
  animation: animloader .3s linear .45s infinite alternate;
  position: absolute;
  top: 50%;
  left: 20px;
  transform: translateY(-50%);
}

.loader:before {
  animation-delay: 0s;
  left: -20px;
}

@keyframes animloader {
  0% {
    height: 48px;
  }

  100% {
    height: 4px;
  }
}

